* {
  box-sizing: border-box;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

body, p {
  margin: 0;
  padding: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  margin: 0 auto;
  padding-left: 50px;
  padding-right: 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.img {
  width: 40px;
  height: 40px;
  display: flex;
}

.navigation__list {
  justify-content: center;
  align-items: center;
  gap: 20px;
  height: 60px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.navigation__link {
  text-transform: uppercase;
  color: #060b35;
  align-items: center;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
  transition: color .3s;
  display: flex;
  position: relative;
}

.navigation__link:hover, .navigation__link--is-active {
  color: #00acdc;
}

.navigation__link--is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
  display: flex;
}

.card__title {
  cursor: pointer;
  color: #060b35;
  flex-direction: column;
  width: 100%;
  margin-bottom: 4px;
  font-weight: 500;
  line-height: 18px;
  transition: color .3s;
  display: flex;
}

.card:hover {
  transition: transform .3s;
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.card__code {
  color: #616070;
  width: 100%;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__reviews {
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-bottom: 24px;
  padding: 0;
  display: flex;
}

.card__reviews--text {
  color: #060b35;
  justify-content: flex-end;
  margin: 0;
  padding: 0;
  font-size: 10px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 16px;
  display: flex;
}

.card__price--text {
  color: #616070;
  margin: 0;
  padding: 0;
  line-height: 18px;
}

.card__price--value {
  color: #060b35;
  justify-content: flex-end;
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  cursor: pointer;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  transition: background-color .3s, color .3s;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  display: flex;
}

.stars--0 .stars__star:nth-child(-n), .stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  display: inline-block;
}

.container {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  max-width: 1024px;
  height: 100%;
  margin: 0 auto;
  padding-top: 50px;
  padding-left: 40px;
  padding-right: 40px;
  display: grid;
}

